import React from "react";
import Images from "../../assets/images/images";
import Analysis from "../Analysis/Analysis";
import BoardsListing from "../BoardsListing/BoardsListing";
import Products from "../Products/Products";
import VideosSlider from "../VideosSlider/VideosSlider";

const HomePage = () => {
  return (
    <div className="HomePage container--full page">
      <div className="HomePage__content container--content">
        <Hero />
        {/* <Advantages /> */}
        <BoardsListing />
        {/* <Products /> */}
        <VideosSlider />
        {/* <Analysis /> */}
      </div>
    </div>
  );
};

const Hero = () => {
  return (
    <>
      <div className="HomePage__hero" id="#">
        <div className="infoContainer">
          <h1>
            Embedded Stack to <span className="highlight">mine</span> Duino
            Coins
          </h1>
          <p>
            Introducing <span className="highlight">DUCO </span>Miners
            (Duino-Coin Miners), Boards which can mine Duino-Coin, a fully
            original, transparent, centralized, eco-friendly crypto coin project
            that focuses on energy-efficient mining using low-powered embedded
            devices.
          </p>
          <a href="/#boards" className="arrowLink">
            Get Started <span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
          </a>
        </div>
        <div className="graphicsContainer">
          <img src={Images.bigLogo} alt="" />
        </div>
      </div>
    </>
  );
};

const Advantages = () => {
  return (
    <>
      <div className="HomePage__hero">
        <div className="infoContainer">
          <h1>
            Embedded Stack to <span className="highlight">mine</span> Duino
            Coins
          </h1>
          <p>
            Lorem Ipsum is simply dummy text of the printing and typesetting
            industry. Lorem Ipsum has been the industry's standard dummy text
            ever since the 1500s, when an unknown printer took a galley of type
            and
          </p>
          <a>
            Get Starteds <span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
          </a>
        </div>
        <div className="graphicsContainer">
          <img src={Images.bigLogo} alt="" />
        </div>
      </div>
    </>
  );
};

export default HomePage;
