import React from "react";
import Images from "../../assets/images/images";
import { Link, useNavigate } from "react-router-dom";

const Navbar = () => {
  return (
    <header>
      <div className="content">
        <AppLogo />
        <NavMenu />
      </div>
    </header>
  );
};

export const AppLogo = () => {
  return (
    <a href="/#" className="logoContainer">
      <img src={Images.logo} alt="" />
    </a>
  );
};

export const NavMenu = () => {
  let naviagte = useNavigate();

  return (
    <ul className="navMenu">
      {/* <li className="navMenu__item">
        <a href="/#boards">Boards</a>
      </li>
      <li className="navMenu__item">
        <a href="/#about">About</a>
      </li>
      <li className="navMenu__item">
        <a href="/#help">How it works</a>
      </li>
      <li
        className="navMenu__item Button--primary"
        onClick={() => naviagte("/shop")}
      >
        Shop
      </li> */}
      <li className="navMenu__item">
        <a href="https://github.com/Duco-Miner" target="_blank">
          GitHub
        </a>
      </li>
      <li className="navMenu__item">
        <a
          href="https://www.instructables.com/Crypto-Mining-With-the-DUCO-Miners/"
          target="_blank"
        >
          Instructables
        </a>
      </li>
      <li className="navMenu__item">
        <a href="https://youtu.be/5NILZw-GCr8" target="_blank">
          YouTube
        </a>
      </li>
    </ul>
  );
};

export default Navbar;
