import React from "react";

const videoLinkList = [
  "5NILZw-GCr8",
  "3Iw1b2K61tQ",
  "B-pOQ3jTFe0",
  "799DicJ5Pyo",
  "FqPr0JaPl5E",
  "bkIyPUFqB9Y",
];

const VideosSlider = () => {
  return (
    <div className="VideosSlider page">
      <div className="VideosSlider__container">
        <h1>
          Start as a hobby and <br /> keep growing !
        </h1>
        <div className="VideosSlider__slider">
          {videoLinkList.map((link, key) => {
            return <YouTubeVideo key={key} link={link} />;
          })}
        </div>
      </div>
    </div>
  );
};

export const YouTubeVideo = ({ link }) => {
  return (
    <div className="YouTubeVideo__container">
      <iframe
        // width="560"
        // height="315"
        src={`https://www.youtube.com/embed/${link}?start=10`}
        title="YouTube video player"
        frameborder="0"
        allow="fullscreen; accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowfullscreen
      ></iframe>
    </div>
  );
};

export default VideosSlider;
