import React from "react";
import { AppLogo, NavMenu } from "../Navbar/Navbar";
import ImageButton from "../components/ImageButton/ImageButton";
import Images from "../../assets/images/images";
const Footer = () => {
  return (
    <div className="Footer">
      <div className="Footer__content">
        <div className="Footer__navContainer">
          {/* <ImageButton image={Images.logo} /> */}
          {/* <NavMenu /> */}
        </div>
        <div className="Footer__creditsContainer">
          <span>Developed By</span>
          <ImageButton image={Images.mvLogo} url={"https://makervinod.in"} />
          <span>Crafted By</span>

          <ImageButton image={Images.dvLogo} url={"https://devednyavyas.com"} />
        </div>
      </div>
    </div>
  );
};

export default Footer;
