import React from "react";
import Images from "../../assets/images/images";

const BoardsListing = () => {
  return (
    <div className="BoardsListing" id="boards">
      <div className="BoardsListing__card BoardsListing__card--small">
        <div className="imageHolder">
          <img src={Images.avrMiner} />
        </div>
        <span>AVR MINER</span>
        <div className="links">
          <a
            href="https://github.com/Duco-Miner/AVR-Miner"
            className="arrowLink"
            target="_blank"
          >
            PCB Design<span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
          </a>
          <a
            href="https://github.com/Duco-Miner/AVR-Miner-Code"
            className="arrowLink"
            target="_blank"
          >
            Source Code<span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
          </a>
        </div>
      </div>
      <div className="BoardsListing__card BoardsListing__card--large">
        <h1 className="highlight">
          {/* Embedded Stack to <span className="highlight">mine</span> Duino Coins */}
          Simple &amp; Easy to Use. <br /> Low-Powered. <br /> Stackable Design.
        </h1>
        <span>MINER BASEBOARD</span>

        <div className="imageHolder">
          <img src={Images.minerBaseboard}></img>
        </div>
        <div className="links">
          <a
            href="https://github.com/Duco-Miner/Common-Miner-Baseboard"
            className="arrowLink"
            target="_blank"
          >
            PCB Design<span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
          </a>
        </div>
      </div>
      <div className="BoardsListing__card BoardsListing__card--small">
        <div className="imageHolder">
          <img src={Images.esp32Miner}></img>
        </div>
        <span>ESP32 MINER</span>

        <div className="links">
          <a
            href="https://github.com/Duco-Miner/ESP32-Miner"
            className="arrowLink"
            target="_blank"
          >
            PCB Design<span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
          </a>
          <a
            href="https://github.com/Duco-Miner/ESP32-Miner-Code"
            className="arrowLink"
            target="_blank"
          >
            Source Code<span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
            <span className="arrow highlight">&gt;</span>
          </a>
        </div>
      </div>
    </div>
  );
};

export default BoardsListing;
